<template>
  <div>
    <div
      v-if="error"
      class="flex row ca snackbar ontop"
      :class="floatBottom ? 'bottom' : 'top'"
    >
      <Notification
        :message="errorMessage"
        :duration="errorDuration"
        :bottom="floatBottom"
        class="is-danger subtitle"
        @deleted="$emit('dismissed')"
      />
    </div>
    <div
      v-else-if="successMessage"
      class="flex row ca snackbar ontop"
      :class="floatBottom ? 'bottom' : 'top'"
    >
      <Notification
        :message="successMessage"
        :bottom="floatBottom"
        :duration="successDuration"
        class="is-success is-light"
        @deleted="$emit('dismissed')"
      />
    </div>
    <transition
      name="slide-fade-bottom"
      mode="out-in"
    >
      <div
        v-if="!hide"
        class="flex row ca"
        :class="{'snackbar bottom' : floatBottom}"
      >
        <div :class="{'box has-background-link-dark' : floatBottom}">
          <span
            v-if="confirmingCancel"
            class="level"
            :class="{'has-text-light' : floatBottom}"
            v-html="cancelConfirmation"
          />
          <span
            v-if="confirmingSubmit"
            class="level"
            :class="{'has-text-light' : floatBottom}"
            v-html="submitConfirmation"
          />
          <div
            class="field is-grouped is-grouped-centered"
          >
            <div class="control">
              <button
                type="button"
                class="button"
                :class="{'is-loading' : loading === true, 'is-link is-light' : !redGreen, 'is-danger is-light' : redGreen}"
                @click="cancel()"
              >
                {{ cancelText === undefined || confirmingCancel ? 'Cancelar' : cancelText }}
              </button>
            </div>
            <div
              v-if="confirmingCancel"
              class="control"
            >
              <button
                type="button"
                class="button is-danger"
                :class="{'is-loading' : loading === true}"
                @click="confirmingCancel = false; this.$emit('leave');"
              >
                {{ cancelConfButton !== undefined ? cancelConfButton : 'Confirmar' }}
              </button>
            </div>
            <div
              v-else-if="confirmingSubmit"
              class="control"
            >
              <button
                type="submit"
                class="button is-success"
                :class="{'is-loading' : loading === true}"
              >
                {{ submitConfButton !== undefined ? submitConfButton : 'Confirmar' }}
              </button>
            </div>
            <div
              v-else
              class="control"
            >
              <button
                :type="submitConfirmation ? 'button' : 'submit'"
                class="button"
                :class="{'is-loading' : loading === true, 'is-link' : !redGreen, 'is-success is-light' : redGreen}"
                @click="submit()"
              >
                {{ submitText !== undefined ? submitText : 'Submeter' }}
              </button>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import Notification from './Notification.vue';
  export default {
    components: { Notification },
    name: "Submit",
    data() {
      return {
        confirmingCancel : false,
        confirmingSubmit : false
      }
    },
    props : ["error", "errorMessage", "errorDuration",
             "loading", 
             "successMessage", "successDuration",
             "submitText", "cancelText", 
             "redGreen", "hide", "floatBottom",
             "cancelConfirmation", "submitConfirmation",
             "cancelConfButton", "submitConfButton"],
    methods: {
      cancel() {
        if (this.cancelConfirmation) {
          if (this.confirmingCancel) {
            this.confirmingCancel = false;
          }
          else if (this.confirmingSubmit) {
            this.confirmingSubmit = false;
          }
          else
            this.confirmingCancel = true;
        }
        else
          this.$emit('leave');
      },
      submit() {
        if (this.submitConfirmation)
          this.confirmingSubmit = true;
        // submit is html implicit
      }
    },
    watch : {
      error : {
        immediate: true, 
        handler () {
          this.confirmingCancel = false;
          this.confirmingSubmit = false;
        }
      },
      successMessage : {
        immediate: true, 
        handler () {
          this.confirmingCancel = false;
          this.confirmingSubmit = false;
        }
      }
    }
  };
</script>