<template>
  <div class="box">
    <div
      class="flex row ra"
    >
      <button
        v-if="certificate.id && !certificate.available"
        class="button is-link mr-3"
        type="button"
        :class="{'is-loading' : loading_publish}"
        :disabled="changes != 0"
        :title="changes != 0 ? 'Deve guardar o certificado antes de o emitir' : 'Um certificado emitido fica automáticamente disponível para todos os participantes presentes e participantes que futuramente façam registo de presença.'"
        @click="activate()"
      >
        <span class="icon"><i class="fas fa-upload" /></span>
        <span>Emitir</span>
      </button>
      <button
        v-if="certificate.id && certificate.available"
        class="button is-link mr-3"
        type="button"
        :class="{'is-loading' : loading_publish}"
        :disabled="changes != 0"
        :title="changes != 0 ? 'Deve guardar o certificado antes de o emitir' : 'Um certificado cuja emissão é cancelada passa a estar indisponível para impressão e confirmação de validade, mas se for emitido novamente os códigos de validação mantém-se.'"
        @click="activate();"
      >
        <span class="icon"><i class="fas fa-download" /></span>
        <span>Cancelar emissão</span>
      </button>
      <button
        class="button is-primary"
        type="button"
        onclick="window.print()"
      >
        <span class="icon"><i class="fas fa-print" /></span>
        <span>Pré-visualizar</span>
      </button>
    </div>
    <form
      id="editarea"
      ref="form"
      @submit.prevent="submit()"
    >
      <div
        class="flex my-column sb"
      >
        <div class="flex my-column vca">
          <div
            id="header-area"
            class="flex row sb width-100"
          >
            <div
              style="width: 120px;"
              class="is-hidden"
            />
            <img
              src="@/assets/ualg.svg"
              style="width: 100mm"
            >
          </div>
          <div class="spacer" />
          <h1 class="is-size-2">
            <Input
              v-model="certificate.title" 
              type="text"
              maxlength="128"
              class="is-large display-edit"
              placeholder="Título"
              style="text-align: center;"
              required
              @input="changes++"
            />
          </h1>
          <div class="half-spacer" />
          <h2 class="is-size-4">
            <Input
              v-model="certificate.subtitle"
              type="text"
              maxlength="128"
              class="is-medium display-edit"
              placeholder="Subtítulo"
              style="text-align: center;"
              required
              @input="changes++"
            />
          </h2>
          <h1 class="is-size-3">
            Nome do participante
          </h1>
          <div class="half-spacer" />
          <div style="width: 500px; max-width: 100%">
            <textarea
              ref="input"
              v-model="certificate.text"
              class="textarea"
              placeholder="Descrição do evento"
              style="text-align: center;"
              maxlength="256"
              required
              :class="{'is-warning' : !invalid && !validity_valid, 'is-danger' : invalid && !validity_valid}"
              @input="updateValidity(); changes++"
              @invalid="updateValidity(); invalid = true;"
            />  
          </div>
        </div>
        <div class="flex my-column vca date">
          <span>
            <Input
              v-model="certificate.date"
              type="text"
              maxlength="128"
              placeholder="Data"
              style="text-align: center;"
              @input="changes++"
            />
          </span>
          <div class="spacer" />
          <div
            class="flex row sa"
            style="width: 100%;"
          >
            <div
              :class="{'display-edit' : !(certificate.signature1 || certificate.signatureImage1)}"
              class="flex my-column vca"
            >
              <img
                :src="certificate.signatureImage1"
                style="height: 3cm;"
              >
              <Input
                v-model="certificate.signature1"
                class="display-edit"
                type="text"
                maxlength="64"
                placeholder="Assinatura"
                style="text-align: center;"
                @input="changes++"
              />
            </div>
            <div
              :class="{'display-edit' : !(certificate.signature2 || certificate.signatureImage2)}"
              class="flex my-column"
            >
              <img
                :src="certificate.signatureImage2"
                style="height: 3cm;"
              >
              <Input
                v-model="certificate.signature2"
                class="display-edit"
                type="text"
                maxlength="64"
                placeholder="Assinatura"
                style="text-align: center;"
                @input="changes++"
              />
            </div>
          </div>
        </div>
      </div>
      <Submit
        style="width: fit-content;"
        :hide="changes == 0"
        float-bottom="true"
        :error="response && response.id === undefined"
        error-message="Houve um erro na submissão do certificado."
        success-duration="3000"
        :success-message="success"
        :loading="loading"
        submit-text="Guardar"
        red-green="true"
        cancel-confirmation="Pretende eliminar as suas alterações?"
        submit-confirmation="Pretende guardar as suas alterações?"
        cancel-conf-button="Eliminar"
        submit-conf-button="Guardar"
        @leave="cancel()"
        @dismissed="this.response = undefined; this.success = undefined"
      />
    </form>
    <div
      id="printarea"
    >
      <div
        class="flex my-column sb"
      >
        <div class="flex my-column vca">
          <div
            id="header-area"
            class="flex row sb width-100"
          >
            <div
              style="width: 120px;"
              class="is-hidden"
            />
            <img
              src="@/assets/ualg.svg"
              style="width: 100mm"
            >
            <div
              id="qrcode"
              class="flex my-column ca vca is-hidden"
            >
              <vue-qrcode
                :value="'Apenas um exemplo, não pode ser usado para certificação'"
                :options="{ width: 120 }"
              />
              <span
                style="max-width: 120px"
                class="is-size-7"
              >Autenticar Certificado</span>
            </div>
          </div>
          <div class="spacer" />
          <h1 class="is-size-2">
            {{ certificate.title }}
          </h1>
          <div class="half-spacer" />
          <h2 class="is-size-4">
            {{ certificate.subtitle }}
          </h2>
          <h1 class="is-size-3">
            Nome do participante
          </h1>
          <div class="half-spacer" />
          <span>{{ certificate.text }}</span>
        </div>
        <div class="flex my-column vca date">
          <span>{{ certificate.date }}</span>
          <div class="spacer" />
          <div
            class="flex row sa"
            style="width: 100%;"
          >
            <div
              v-if="certificate.signature1 || certificate.signatureImage1"
              class="flex my-column vca"
            >
              <img
                :src="certificate.signatureImage1"
                style="height: 3cm;"
              >
              <span>{{ certificate.signature1 }}</span>
            </div>
            <div
              v-if="certificate.signature2 || certificate.signatureImage2"
              class="flex my-column"
            >
              <img
                :src="certificate.signatureImage2"
                style="height: 3cm;"
              >
              <span>{{ certificate.signature2 }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Input from "@/components/Input";
import Submit from "@/components/Submit";
import { getCookie } from "@/common";
import { API } from "@/http-common";
import VueQrcode from '@chenfengyuan/vue-qrcode';

export default {
  name: "CreateCertificate",
  components: {VueQrcode, Input, Submit},
  data() {
    return {
      certificate : {
        event : this.$route.params.id
      },
      response : undefined,
      success : undefined,
      changes : 0,
      loading : undefined,
      invalid: false,
      validity_valid : true,
      loading_publish : false
    }
  },
  created() {
    document.title = "Criar Certificado de Participação";
    API.get("certificate/?event=".concat(this.$route.params.id))
      .then((response) => {
          if (response.data[0]) {
            this.certificate = response.data[0]
             document.title = "Editar Certificado de Participação";
          }
      }
    ).catch();
  },
  methods : {
    cancel() {
      this.loading = true;
      if (this.$route.params.id) {
        API.get("certificate/?event=".concat(this.$route.params.id))
          .then((response) => {
              this.certificate = response.data[0]
              this.changes = 0;
              this.loading = undefined;
          }
        );
      }
      else
        this.$router.back();
    },
    submit() {
      if (this.$refs.form.checkValidity()) {
        this.loading = true;
        setTimeout(this.postSubmission.bind(this), 0);
      }
    },
    activate() {
      this.loading_publish = true;
      var method;
      var url;
      var successMessage;
      method = "patch";
      url = "/certificate/".concat(this.certificate.id).concat("/");
      successMessage = "O certificado foi " + (this.certificate.available ? 'desativado' : 'emitido') + " com sucesso. Pode continuar a editar.";
      var options = {
        method : method,
        url : url,
        headers: {
          "X-CSRFToken": getCookie("csrftoken"),
        },
        data: {"available" : !this.certificate.available},
        transformResponse: [
          async (data) => {
            this.loading = undefined;
            try {
              this.response = JSON.parse(data);
              this.certificate = this.response;
              this.success = successMessage;
              this.loading_publish = false;
            } catch (e) {this.response = data;}
            return this.response;
          },
        ],
      };
      API(options);
    },
    postSubmission() {
      var method;
      var url;
      var successMessage;
      if (this.certificate.id === undefined)  {
          method = "post";
          url = "/certificate/";
          successMessage = "O certificado foi criado com sucesso. Pode continuar a editar.";
      }
      else {
        method = "put";
        url = "/certificate/".concat(this.certificate.id).concat("/");
        successMessage = "O certificado foi editado com sucesso. Pode continuar a editar.";
      }
      var options = {
        method : method,
        url : url,
        headers: {
          "X-CSRFToken": getCookie("csrftoken"),
        },
        data: this.certificate,
        transformResponse: [
          async (data) => {
            this.loading = undefined;
            try {
              this.response = JSON.parse(data);
              this.certificate = this.response;
              this.success = successMessage;
              this.changes = 0;
            } catch (e) {this.response = data;}
            return this.response;
          },
        ],
      };
      API(options);
    },
    valid() {
      return !this.invalid || this.$refs['input'].validity.valid;
    },
    updateValidity() {
      this.validity_valid = this.$refs['input'].validity.valid;
    }
  }
};
</script>

<style lang="scss">
  @import url(//db.onlinewebfonts.com/c/3b0ae6af94805b08b11cdf1d97c47dce?family=PreloSlabW01-Medium);

  #printarea>div {
    padding: 1cm;
  }
  #header-area {
    padding-left: 2mm;
    padding-right: 2mm;
  }

  .display-print {
    display: none;
  }

  #printarea {
    display: none;
  }

  @media print {
    #printarea {
      display: block;
      overflow: hidden;
    }
    .is-hidden-mobile, .is-hidden {
      display: flex !important;
    }
    .display-edit {
      display: none;
    }

    .display-print {
      display: flex;
    }

    .sb {
      justify-content: space-between !important;
    }

    #page-content {
      margin: 0;
    }
    #page-content * {
      visibility: hidden;
    }
    #printarea, #printarea * {
      visibility: visible;
    }

    #printarea {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }

    #printarea>div {
      height: 100vh;
      overflow: hidden;
      margin: 0;
      padding: 0;
    }

    #msg, #main-navbar, #top-hero, footer {
      display: none;
    }

    .date {
      margin-top: 0;
    }
  }

  #printarea {
    background-color: white;
    white-space: break-spaces;
    word-break: break-word;
  }
  h1, h2, h3, span {
    max-width: 20cm;
    text-align: center;
  }
  @page {
    size: A4 auto;
  }

  .date {
    margin-top: 1cm;
  }
  
  h1, h2, h3 {
    margin: 3mm;
  }

  .half-spacer{
    height: 5mm;
  }

  .spacer{
    height: 1cm;
  }

  #printarea * {
    box-sizing: border-box;
    font-family: "PreloSlabW01-Medium";
    page-break-after: avoid;
  }

  .flex {
    display: flex;
  }

  .row {
    flex-direction: row;
  }

  .my-column {
    flex-direction: column;
  }

  .ca {
    justify-content: center;
  }

  .sb {
    justify-content: center;
  }

  .sa {
    justify-content: space-around;
  }

  .vca {
    align-items: center;
  }
</style>

<style lang="scss">
  @import '@/styles/flex-styles.scss';
  @import '@/styles/global-styles.scss';
</style>