<template>
  <transition
    :name="bottom ? 'slide-fade-bottom' : 'slide-fade'"
    class="box notification"
    mode="out-in"
  >
    <span v-show="active">
      <button
        type="button"
        class="delete"
        @click="deactivate()"
      />
      {{ message }}
    </span>
  </transition>
</template>

<script>
  export default {
    name: "Notification",
    props : ["message", "duration", "bottom"],
    emits : ["deleted"],
    data() {
      return {
        active : false
      }
    },
    async mounted() {
      this.active = true;
      if (this.duration) {
        await new Promise(r => setTimeout(r, this.duration));
        this.deactivate();
      }
    },

    methods : {
      async deactivate() {
        this.active = false;
        await new Promise(r => setTimeout(r, 500));
        this.$emit('deleted');
      }
    }
  };
</script>